// src/services/claudeAPI.js
/**
 * Claude API Service (Proxy-only)
 * The browser calls our Cloudflare Worker; the Worker adds the real API key.
 */
import axios from 'axios';
import { CLAUDE_CONFIG, ERROR_MESSAGES } from '@config/constants';

class ClaudeAPIService {
  constructor() {
    // Use your Worker URL from .env (fallback to relative path)
    this.baseURL = CLAUDE_CONFIG.baseURL || '/';
    this.model   = CLAUDE_CONFIG.model;

    this.client = axios.create({
      baseURL: this.baseURL,            // e.g. https://smart-resume-proxy.<yourname>.workers.dev
      headers: { 'Content-Type': 'application/json' },
      timeout: 60000,
    });
  }

  /** In proxy mode, the client is always "configured". */
  isConfigured() {
    return true;
  }

  async sendMessage(prompt, options = {}) {
    try {
      const body = {
        model: this.model,
        max_tokens: options.maxTokens ?? CLAUDE_CONFIG.maxTokens,
        temperature: options.temperature ?? CLAUDE_CONFIG.temperature,
        messages: [{ role: 'user', content: prompt }],
      };

      // IMPORTANT: call the Workers Claude passthrough route
      const res = await this.client.post('/v1/messages', body, {
        headers: { 'anthropic-version': '2023-06-01' } // Worker forwards this too
      });
      return res.data?.content?.[0]?.text ?? '';
    } catch (error) {
      console.error('Claude API Error (proxy):', error);
      if (error.response) {
        const status = error.response.status;
        const message = error.response.data?.error || error.response.data?.message || 'Unknown error';
        if (status === 401) throw new Error('Proxy unauthorized. Check your Worker secret CLAUDE_API_KEY.');
        if (status === 403) throw new Error('Proxy blocked the request. Verify CORS in the Worker.');
        if (status === 429) throw new Error('Rate limit exceeded. Please try again later.');
        if (status >= 500) throw new Error('Claude service unavailable. Please try again later.');
        throw new Error(`API Error: ${message}`);
      }
      if (error.request) throw new Error(ERROR_MESSAGES.NETWORK_ERROR);
      throw new Error(ERROR_MESSAGES.API_ERROR);
    }
  }

  async analyzeResume(resumeText, jobDescription) {
    // Keep payloads reasonable (prevents too large issues)
    const resumeTrim = (resumeText || '').slice(0, 16000);
    const jdTrim     = (jobDescription || '').slice(0, 6000);

    const prompt = `You are an expert resume analyzer and career coach. Analyze the following resume against the job description and provide detailed insights.

RESUME:
${resumeTrim}

JOB DESCRIPTION:
${jdTrim}

Return ONLY valid JSON in this exact schema:
{
  "keywordAnalysis": { "matched": [], "missing": [], "relevanceScore": 0 },
  "atsScore": { "overall": 0, "breakdown": { "formatting": 0, "keywords": 0, "structure": 0, "contact": 0 } },
  "strengths": [],
  "weaknesses": [],
  "recommendations": [],
  "aiEnhancedSuggestions": []
}`;

    const text = await this.sendMessage(prompt, {
      maxTokens: 4096,
      temperature: 0.3,
    });

    // Tolerate code fences from the model
    const cleaned = text.trim().replace(/```json\s*|```\s*$/g, '');
    try {
      return JSON.parse(cleaned);
    } catch (e) {
      console.error('AI JSON parse failed. Raw:', text);
      throw new Error('AI returned an unexpected response. Please try again.');
    }
  }

  async enhanceBulletPoints(originalBullets, jobKeywords) {
    const prompt = `Enhance these resume bullets with natural keywords and measurable impact.

ORIGINAL:
${(originalBullets||[]).join('\n')}

KEYWORDS:
${(jobKeywords||[]).join(', ')}

Return JSON array like:
[{"original":"","enhanced":"","keywords_added":[]}]`;

    const text = await this.sendMessage(prompt, { temperature: 0.7 });
    const cleaned = text.trim().replace(/```json\s*|```\s*$/g, '');
    try { return JSON.parse(cleaned); } catch { return []; }
  }

  async generateSummary(resumeText, jobDescription) {
    const prompt = `Create a 34 sentence ATS-friendly professional summary using relevant keywords.

RESUME:
${(resumeText||'').slice(0, 2000)}

JOB DESCRIPTION:
${(jobDescription||'').slice(0, 4000)}

Return only the summary text.`;
    return this.sendMessage(prompt, { maxTokens: 500, temperature: 0.7 });
  }

  async getCareerAdvice(resumeText, targetRole) {
    const prompt = `Provide 35 actionable tips for this background targeting "${targetRole}".

BACKGROUND:
${(resumeText||'').slice(0, 2000)}`;
    return this.sendMessage(prompt, { maxTokens: 1000, temperature: 0.8 });
  }
}

export const claudeAPI = new ClaudeAPIService();
export default ClaudeAPIService;
