// src/components/upload/FileUpload.jsx
import React, { useCallback, useMemo, useRef, useState } from 'react';
import Alert from '../Alert';
import { formatFileSize } from '@utils/helpers';
import { FILE_CONFIG } from '@config/constants';

const getExt = file =>
  (file?.name && '.' + file.name.split('.').pop().toLowerCase()) || '';

export function FileUpload({ onFileUpload, isLoading, error: uploadError }) {
  const inputRef = useRef(null);
  const [localError, setLocalError] = useState(null);
  const [fileName, setFileName] = useState('');

  const acceptAttr = useMemo(
    () => (FILE_CONFIG.supportedFormats || ['.pdf','.docx','.doc','.txt']).join(','),
    []
  );

  const validate = file => {
    if (!file) return 'No file provided.';
    if (file.size > FILE_CONFIG.maxSize)
      return `File too large. Max is ${formatFileSize(FILE_CONFIG.maxSize)}.`;

    const ext = getExt(file);
    const allowed = Array.isArray(FILE_CONFIG.supportedFormats)
      ? FILE_CONFIG.supportedFormats
      : ['.pdf', '.docx', '.doc', '.txt'];

    if (!allowed.includes(ext)) {
      return `Unsupported file type (${ext || 'unknown'}). Allowed: ${allowed.join(', ')}`;
    }
    return null;
  };

  const handleFiles = useCallback(
    async files => {
      setLocalError(null);
      const file = files?.[0];
      const problem = validate(file);
      if (problem) {
        setLocalError(problem);
        return;
      }
      setFileName(file.name);
      try {
        await onFileUpload?.(file);
      } catch (err) {
        setLocalError(err?.message || 'Upload failed.');
      }
    },
    [onFileUpload]
  );

  const onChange = e => handleFiles(e.target.files);
  const onDrop = e => {
    e.preventDefault();
    handleFiles(e.dataTransfer?.files);
  };

  return (
    <div
      className="border-2 border-dashed rounded-xl p-6 text-center animate-fade-in"
      onDragOver={e => e.preventDefault()}
      onDrop={onDrop}
    >
      <input
        ref={inputRef}
        type="file"
        accept={acceptAttr}
        className="hidden"
        onChange={onChange}
      />
      <button
        type="button"
        className="px-4 py-2 rounded-lg bg-cyan-500 text-white disabled:opacity-60"
        onClick={() => inputRef.current?.click()}
        disabled={isLoading}
      >
        {isLoading ? 'Uploading' : 'Click to browse'}
      </button>
      {fileName && (
        <p className="mt-2 text-sm text-gray-600">Selected: {fileName}</p>
      )}
      {(localError || uploadError) && (
        <div className="mt-3">
          <Alert type="error" message={localError || uploadError} />
        </div>
      )}
      <p className="mt-3 text-xs text-gray-500">
        Supports {acceptAttr} (Max {formatFileSize(FILE_CONFIG.maxSize)})
      </p>
    </div>
  );
}

export default FileUpload;
